html {
    font-size: 100%;
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', serif;
    line-height: 1.3;
    background-color: $bg1;
    //additional:
    // height: 100vh;
    // width: 100vw;
    // display: flex;
    // flex-direction: column;
    // align-items: center;
    ///additional
}

*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;	
    padding: 0;  
  }

// Flexbox
.flex {
    display: flex;

    &-jc-sb {
        justify-content: space-between;
    }

    &-jc-c {
        justify-content: center;
    }

    &-ai-c {
        align-items: center;
    }
}

// Visibility

.hide-for-mobile {
    @include breakpoint-down(medium){
        display: none;
    }
}










